import React, { Component } from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import './ProgramCarousel.css'

import pic1 from '../pictures/2019_Conference.jpg';
import pic2 from '../pictures/banner-bg4.jpg';
import pic3 from '../pictures/lamp_logo.svg';
import pic4 from '../pictures/2017_Beach_Cleanup.JPG';
import pic5 from '../pictures/2019_fire_bonfire.jpg.HEIC';
import pic6 from '../pictures/2018_Kayaking.JPG'



class ProgramCarousel1 extends Component {
    render() {
        return (
            <Carousel>
                <div>
                    <img src = {pic1}/>
                    <p className="legend">LAMP</p>
                </div>
                <div>
                    <img src = {pic2} />
                    <p className="legend">SPIEL</p>
                </div>
                <div>
                <img src = {pic3} />
                    <p className="legend">LAMP</p>
                </div>
                <div>
                <img src = {pic4} />
                    <p className="legend">beach cleanup, 2017</p>
                </div>

            </Carousel>
        );
    }
}
export default ProgramCarousel1;

class ProgramCarousel2 extends Component {
    render() {
        return (
            <Carousel>
                <div>
                    <img src = {pic5}/>
                    <p className="legend">a.c.e. committee</p>
                </div>
                <div>
                    <img src = {pic6} />
                    <p className="legend">a.c.e. committee</p>
                </div>

            </Carousel>
        );
    }
}
export { ProgramCarousel2 };